<template>
  <div class="loginbox">
    <header class="mui-bar mui-bar-nav">
      <router-link to="/login/login" class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"></router-link>
      <h1 class="mui-title"></h1>
    </header>
    <div class="mui-content">
      <form id='login-form' class="mui-input-group">
        <div class="mui-input-row">
          <label></label>
          <input id='account' type="text" class="mui-input-clear mui-input" placeholder="" v-model="tel">
        </div>
        <div class="mui-input-row">
          <label></label>
          <input id='password' type="password" class="mui-input-clear mui-input" placeholder="" v-model="password">
        </div>
        <div class="mui-input-row">
          <label></label>
          <input id='accountsure' type="text" class="mui-input-clear mui-input" placeholder="" v-model="code">
          <span @click="getcode()"class="font" style="width:1.4rem;display:block;height:80%;text-align: center;line-height:0.88rem;">{{codeBtn}}</span>
        </div>
      </form>

      <div class="mui-content-padded">
        <button @click="resetting()" id='login'></button>
      </div>
      <!--<div class="mui-content-padded oauth-area">-->

    </div>
  </div>

</template>

<script>
  import apicommon from "../../assets/common/js/api.js";
  import mui from 'mui';
  export default{
    data(){
      return{
        tel:"",
        password:"",
        code:"",
        codeBtn:"",
      }
    },
    created:function(){
//      //
//      mui.back = function(){
//        history.go(-1);
//      }
    },
    methods:{
      resetting:function(){
        var url=apicommon.apicommon+"/test/index.php/Activity/MobileList/resetpwd";
        var data={tel:this.tel,password:this.password,code:this.code};
        this.axios.post(url,this.$qs.stringify(data)).then(successRes=>{
          var that=this;
          if(successRes.data.status=="1"){
            that.$router.push('/login/login');
          }else if(successRes.data.status=="2"){
            alert(",,");
          }else if(successRes.data.status=="3"){
            alert(",");
          }else if(successRes.data.status=="4"){
            alert(",")
          }else if(successRes.data.status=="5"){
            alert(",")
          }else if(successRes.data.status=="6"){
            alert("")
          }
        })
      },

      getcode:function(){
        var url=apicommon.apicommon+"/test/index.php/Activity/MobileList/findpwdgetcode";
        var data={tel:this.tel};
        this.axios.post(url,this.$qs.stringify(data)).then(successRes=>{
          if(successRes.data.status===1){
            this.codeBtn=60;
            var that=this;
            var t=setInterval(function(){
              that.codeBtn--;
            },1000);
            if(this.codeBtn==0){
              that.codeBtn=="";
              clearInterval(t);
            }
          }else if(successRes.data.status===2){
            alert("");
          }else if(successRes.data.status===3){
            alert(",");
          }
        });
      }
    }
  }
</script>

<style scoped>
  body {
    background: #F5F5F5;
  }
  .mui-pull-left{
    color:#ec5155;
  }
  .mui-bar-nav ~ .mui-content {
    padding-top: 60px;
    font-size:14px;
  }
  .mui-input-row label ~ input{
   font-size:14px;
  }
  .mui-btn-block {
    padding: 8px 0;
    font-size: 16px;
  }
  .link-area {
    display: block;
    margin-top: 25px;
    text-align: center;
  }


  .spliter {
    color: #bbb;
    padding: 0px 8px;
  }

  .oauth-area {
    position: absolute;
    bottom: 20px;
    left: 0px;
    text-align: center;
    width: 100%;
    padding: 0px;
    margin: 0px;
  }

  .oauth-area .oauth-btn {
    display: inline-block;
    width: 50px;
    height: 50px;
    background-size: 30px 30px;
    background-position: center center;
    background-repeat: no-repeat;
    margin: 0px 20px;
    /*-webkit-filter: grayscale(100%); */
    border: solid 1px #ddd;
    border-radius: 25px;
  }

  .oauth-area .oauth-btn:active {
    border: solid 1px #aaa;
  }

  .oauth-area .oauth-btn.disabled {
    background-color: #ddd;
  }
  #login{
    color: #fff;
    background: #ec5155;
    border: none;
    border-radius: 8px;
    width:95%;
    padding:10px;
    margin:0 2.5%;
    font-size: 0.3rem;
  }

  .font{
    width: 1.4rem;
    display: block;
    height: 80%;
    text-align: center;
    line-height: 0.88rem;
    position: absolute;
    right: 0.2rem;
    color: #666;
    font-size: 12px;
  }
</style>



