<template>
  <div class="message">
    <mt-header title="">
      <mt-button @click="back" slot="left" icon="back"></mt-button>
      <mt-button @click="save" slot="right"></mt-button>
    </mt-header>

    <form class="mui-input-group">
      <div class="mui-input-row">
        <label></label>
        <input type="text" class="mui-input-clear" placeholder="" v-model="sex">
      </div>
    </form>

    <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
  </div>
</template>

<script>
  import mui from 'mui'; // 
  import apicommon from "../../assets/common/js/api.js";
  export default {
    data(){
      return{
        sex:"",
        userId:localStorage.getItem('userId'),
        slots: [
          {
            flex: 1,
            values: ['', ''],
            className: 'slot1',
            textAlign: 'center'
          },
        ]
      }
    },
    created:function(){
//      //
//      mui.back = function(){
//        history.go(-1);
//      }
      this.getinfo();
      //mui
      mui.init();
      var nativeWebview, imm, InputMethodManager;
      var initNativeObjects = function() {
        if (mui.os.android) {
          var main = plus.android.runtimeMainActivity();
          var Context = plus.android.importClass("android.content.Context");
          InputMethodManager = plus.android.importClass("android.view.inputmethod.InputMethodManager");
          imm = main.getSystemService(Context.INPUT_METHOD_SERVICE);
        } else {
          nativeWebview = plus.webview.currentWebview().nativeInstanceObject();
        }
      };
      var showSoftInput = function() {
        if (mui.os.android) {
          imm.toggleSoftInput(0, InputMethodManager.SHOW_FORCED);
        } else {
          nativeWebview.plusCallMethod({
            "setKeyboardDisplayRequiresUserAction": false
          });
        }
        setTimeout(function() {
          var inputElem = document.querySelector('input');
          inputElem.focus();
          inputElem.parentNode.classList.add('mui-active'); //search
        }, 200);
      };
      mui.plusReady(function() {
        initNativeObjects();
        showSoftInput();
      });
    },
    methods:{
      back:function () {
        history.go(-1);
      },
      onValuesChange(picker,values){
        this.sex=values;
      },
      getinfo:function () {
        var url=apicommon.apicommon+"/test/index.php/Activity/MobileList/userInfo";
        this.axios.post(url,this.$qs.stringify({uid:this.userId})).then(successRes=>{
          if(successRes.data.data.sex==""){
            this.sex="";
          }else{
            this.sex=successRes.data.data.sex;
          }
        })
      },
      save:function () {
        var url=apicommon.apicommon+"/test/index.php/Activity/MobileList/changeInfo";
        console.log(this.userId);
        console.log(this.sex[0]);
        this.axios.post(url,this.$qs.stringify({uid:this.userId,status:1,title:this.sex[0]})).then(successRes=>{
          if(successRes.data.status==1){
            this.$router.push("/index/message");
          }else if(successRes.data.status==0){
            mui.toast("");
            this.$router.push("/index/message");
          }
        })
      }
    }
  }
</script>

<style scoped>



  body {
    background: #F5F5F5;
  }
  .mui-input-row{
    font-size:0.28rem;
    margin-top:0.2rem;
    line-height: 0.8rem;
  }
  .mui-input-clear{
    font-size:0.28rem;
  }
  .mui-input-row label {
    width: 20%;
    line-height: 0.4rem;
  }
  .mui-input-row label ~ input, .mui-input-row label ~ select, .mui-input-row label ~ textarea{
    width:75%;
  }
  /**/
  .mint-header-title{
    font-size:0.34rem;
  }
  .mint-header{
    background: #fff;
    color: #ec5155;
    height:45px;
    line-height: 45px;
    font-size:0.34rem;
  }
  .mint-button-text{
    line-height: 45px;
    font-size:0.26rem;
  }
  .mint-button{
    line-height: 45px;
    font-size:0.26rem;
  }

  .picker-slot.picker-slot-right {
    text-align: center;
  }
</style>




