<template>
  <ul class="single">
    <li v-for="(item,index) in list" :style="'backgroundImage:url('+item.icon+')'">
      <img @click="share(index)" src="../../assets/img/huigongxiang/huigongxiang-btn.png" alt="" class="btn">
      <router-link v-bind="{to:'/activity/'+index}">
        <img class="kuang" src="../../assets/img/huigongxiang/huigongxiang-kuang.png" alt="">
        <div class="name">{{item.title}}</div>
      </router-link>
    </li>
  </ul>
</template>
<script>
  import apicommon from "../../assets/common/js/api.js";
  import mui from 'mui'; // 
  export default {
    data(){
      return{
        list:[],
        userId:localStorage.getItem('userId'),
      }
    },
    created:function(){
      var url=apicommon.apicommon+"/test/index.php/Activity/Game/getactive";
      this.axios.post(url).then(successRes=>{
        this.list=successRes.data.data;
      });
    },

    methods:{
      share:function(index){
        mui.init();
        var Intent = null,
          File = null,
          Uri = null,
          main = null;
        var shares = null;
        var shareImageUrl = '';
        mui.plusReady(function() {
          updateSerivces();
          if (plus.os.name == "Android") {
            Intent = plus.android.importClass("android.content.Intent");
            File = plus.android.importClass("java.io.File");
            Uri = plus.android.importClass("android.net.Uri");
            main = plus.android.runtimeMainActivity();
          }
        });
        /**
         * 
         */
        function updateSerivces() {
          plus.share.getServices(function(s) {
            shares = {};
            for (var i in s) {
              var t = s[i];
              shares[t.id] = t;
            }
            outSet("");
          }, function(e) {
            outSet("" + e.message);
          });
        }
        /**
         * 
         */
        function shareAction(id, ex) {
//          console.log('id = ' + id);
//          console.log('ex = ' + ex);
//          console.log('shares[id]' + shares[id]);
          var s = null;
          if (!id || !(s = shares[id])) {
            outLine("");
            return;
          }
          if (s.authenticated) {
            outSet("------");
            shareMessage(s, ex);
          } else {
            outSet("------");
            s.authorize(function() {
              shareMessage(s, ex);
            }, function(e) {
              outLine("");
            });
          }
        }
        /**
         * 
         */
        var uid=this.userId;
        var that=this;
        function shareMessage(s, ex) {
          var msg = {
            href: that.list[index].link,
            title: that.list[index].title,
            content: that.list[index].content,
            thumbs: that.list[index].pic,
            pictures: that.list[index].icon,
            extra: {
              scene: ex
            }
          };
          s.send(msg, ()=> {
            var url=apicommon.apicommon+"/test/index.php/Activity/Game/shareback"
            if(s.description==""){
              that.axios.post(url,that.$qs.stringify({uid:that.userId,type:1,cate:2})).then(successRes=>{
                if(successRes.data.status==1){
                  outLine(""+s.description+"!20");
                }else if(successRes.data.status==2){
                  outLine(""+s.description+"!20");
                }else if(successRes.data.status==3){
                  outLine(""+s.description+"!");
                }
            });
              outLine(that.userId);
            }else if(s.description=="QQ"){
              outLine(that.userId);
              that.axios.post(url,that.$qs.stringify({uid:that.userId,type:0,cate:2})).then(successRes=>{
                if(successRes.data.status==1){
                  outLine(""+s.description+"!20");
                }else if(successRes.data.status==2){
                  outLine(""+s.description+"!20");
                }else if(successRes.data.status==3){
                  outLine(""+s.description+"!");
                }
              })
            }

          }, (e)=> {
            outLine(""+s.description+"!");
          });
        }
        /**
         * 
         * id
         */
        function shareHref() {
          var ids = [
              {id: "weixin", ex: "WXSceneSession"  /**/},
              {id: "weixin", ex: "WXSceneTimeline" /**/},
              {id: "qq"   /*QQ*/}],
            bts = [
              {title: ""},
              {title: ""},
              {title: "QQ"},
            ];
          plus.nativeUI.actionSheet({
              cancel: "",
              buttons: bts
            },
            function(e) {
              var i = e.index;
//              console.log('i = ' + i);
              if (i > 0) {
                shareAction(ids[i - 1].id, ids[i - 1].ex);
              }
            }
          );
        }
// 
        function outSet(msg) {
//          console.log(msg);
        }
// 
        function outLine(msg) {
          mui.toast(msg);
        }
        shareHref();
      },
    }

  }
</script>

<style scoped>
  .btn{
    width:1.86rem;
    position:absolute;
    bottom:-0.22rem;
    right:0.85rem;
    z-index: 3;
  }
  .name{
    position:absolute;
    bottom:-0.1rem;
    left:1.2rem;
    font-size:0.28rem;
    color:rgb(51,51,51);
  }
  .single{
    width:100%;
    height:auto;
    overflow: auto;
  }
  .single img.kuang{
    width:7.1rem;
    height:3.86rem;
    position:absolute;
    left:50%;
    margin-left:-3.55rem;
  }
  .single li{
    width: 7rem;
    border-radius: 15px;
    height: 3.6rem;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    left: 50%;
    margin-left: -3.55rem;
    margin-bottom:0.5rem;
  }
</style>



