<template>
  <div class="enchashment">
    <img @click="back" src="../../assets/img/huigongxiang/huigongxiang-back.png" alt="" id="back">

    <div class="money">: <span>{{money}}</span> </div>
    <div class="input">
      <input type="text" v-model="user" placeholder="">
      <input type="text" v-model="phone" placeholder="">
      <input type="text" v-model="num" placeholder="">
      <p><img src="../../assets/img/Jackpot/enchashment-tishi.png" alt="">1000</p>
      <img @click="getcash" src="../../assets/img/Jackpot/enchashment-btn.png" alt="" class="btn">
    </div>
    <img @click="getrule" src="../../assets/img/Jackpot/Jackpot-rule.png" alt="" class="rule">

    <div class="rulebox" v-show="showrule">
      <div>
        <p class="title"></p>
        <p>1.20</p>
        <p>2.,,,</p>
        <p>3.,,72</p>
        <p>4.20,~</p>
        <p></p>
        <img @click="hiderule" src="../../assets/img/index/index-close.png" alt="" class="close">
      </div>
    </div>
  </div>

</template>

<script>
  import apicommon from "../../assets/common/js/api.js";
  import mui from 'mui'; // 
  export default {
    data(){
      return{
        userId:localStorage.getItem('userId'),//id
        money:"0",
        user:"",
        phone:"",
        num:"",
        showrule:false,
      }
    },
    created:function(){
      this.getmoney();
//      //
//      mui.back = function(){
//        history.go(-1);
//      }
    },
    methods:{
      back:function () {
        this.$router.go(-1)
      },
      getmoney:function () {
        var url=apicommon.apicommon+"/test/index.php/Activity/Game/getnowprice";
        this.axios.post(url,this.$qs.stringify({uid:this.userId})).then(successRes=>{
          if(successRes.data.status===1){
            this.money=successRes.data.nowprice;
          }
        })
      },
      getcash:function () {
        var url=apicommon.apicommon+"/test/index.php/Activity/Game/getcash";
        var data={uid:this.userId,zfbid:this.user,phone:this.phone,num:this.num};
        if(this.userId==""){
          mui.toast('');
          return;
        }else if(this.phone==""){
          mui.toast('');
          return;
        }else if(this.num==""){
          mui.toast('');
          return;
        }
        this.axios.post(url,this.$qs.stringify(data)).then(successRes=>{
          console.log(successRes.data.status);
          if(successRes.data.status==1){
            mui.toast('');
          }else if(successRes.data.status==2){
            mui.toast(',');
          }else if(successRes.data.status==3){
            mui.toast('20!');
          }else if(successRes.data.status==4){
            mui.toast('')
          }else if(successRes.data.status==5){
            mui.toast('')
          }
        })
      },
      getrule:function () {
        this.showrule=!this.showrule;
      },
      hiderule:function () {
        this.showrule=!this.showrule;
      },

    },
  }
</script>

<style scoped>
  .enchashment{
    width: 100%;
    height:100%;
    background-image: url("../../assets/img/Jackpot/Jacket-newbj.jpg");
    -webkit-background-size: cover;
    background-size: cover;
    background-repeat: no-repeat;
    position: fixed;
    top:0;
    left:0;
  }
  .input img.btn{
    width: 1.98rem;
    height: 0.82rem;
    position: absolute;
    margin-top: 0.2rem;
    left: 50%;
    margin-left: -0.99rem;
  }
  #back{
    width:0.6rem;
    position:absolute;
    top:0.4rem;
    left:0.4rem;
  }
  .input{
    width:6.4rem;
    height:5.78rem;
    background-image: url("../../assets/img/Jackpot/enchashment-juanzhou.png");
    -webkit-background-size: 100% 100%;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    position: absolute;
    top:3rem;
    left:50%;
    margin-left:-3.2rem;
    padding-top:0.5rem;
  }
  .input input{
    width:80%;
    margin: 0 10% 10px 10%;
    background: none;
    border:none;
    outline: none;
    border-bottom:2px solid #b1742e;
    box-sizing: border-box;
    color: #b1742e;
  }


  input::placeholder{
    color: #b1742e;
    font-size:0.3rem;
  }
  .input p{
    height: 0.4rem;
    line-height: 0.4rem;
    color: #b1742e;
    font-size:0.3rem;
    text-indent:0.1rem;
  }
  .input img{
    width:0.32rem;
    height: 0.32rem;
    margin-top: 0.04rem;
    margin-left:0.7rem;
    float: left;
  }

  .money{
    width: 3.34rem;
    height: 0.82rem;
    background-image: url("../../assets/img/Jackpot/Jacket-currentjifen.png");
    background-size: cover;
    background-repeat: no-repeat;
    text-indent: 0.2rem;
    line-height: 0.8rem;
    position: absolute;
    bottom: 0.6rem;
    left: 0.8rem;
    font-size: 0.3rem;
    color: #451206;
  }

  .money span{
    margin-left: 0.05rem;
  }
  .rule{
    width: 0.8rem;
    height: 0.82rem;
    position: absolute;
    bottom: 0.6rem;
    left: 5.5rem;
  }
  .rulebox{
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.3);
    position: absolute;
    top:0;
    left:0;
  }
  .rulebox div{
    width:5.6rem;
    height:6.7rem;
    position: absolute;
    top:-1rem;
    left:0;
    right:0;
    bottom:0;
    margin:auto;
    background-image: url("../../assets/img/charts/rule-bg.png");
    background-size: cover;
    background-repeat: no-repeat;
  }
  .rulebox div p{
    width:90%;
    color:#fff;
    font-size:0.26rem;
    margin:0.05rem 5%;
  }
  .rulebox div .title{
    font-size:0.32rem;
    text-align: center;
    margin: 0.2rem 5% 0.4rem 5%;
  }

  .close{
    width:0.8rem;
    height:0.8rem;
    position: absolute;
    bottom:-1.2rem;
    left:50%;
    margin-left:-0.4rem;
  }
</style>



