<template>
    <div class="huigongxiang">
      <div class="header">
        <img @click="back" src="../../assets/img/huigongxiang/huigongxiang-back.png" alt="" id="back">
        <div class="title"></div>
        <img @click="getrule" src="../../assets/img/huigongxiang/huigongxiang-rule.png" alt="" class="rule">
        <div class="list">
          <div v-for="(item,index) in list" @click="isActive(index)" :class="{active:item.isactive}">{{item.name}}</div>
        </div>
      </div>
      <div id="list" ref="mybox">
        <singleItem  v-show="fu"> </singleItem>
        <fuItem v-show="single"> </fuItem>
      </div>
      <!---->
      <div class="rulebox" v-show="showrule">
        <div>
          <p class="title"></p>
          <p></p>
          <p>1.</p>
          <p>2.</p>
          <p>3.10102-5</p>
          <p>~</p>
          <img @click="hiderule" src="../../assets/img/index/index-close.png" alt="" class="close">
        </div>
      </div>
    </div>
</template>

<script>
  import mui from 'mui'; // 
  import singleItem from '@/components/huigongxiang/singleItem'
  import fuItem from '@/components/huigongxiang/fuItem'
  export default {
    data(){
      return{
        list:[
          {name:"",isactive:true,},
          {name:"",isactive:false,},
        ],
        single:true,
        fu:false,
        showrule:false,
      }
    },
    created:function(){

    },
    mounted:function(){
      this.getHeight();
    },
    methods:{
      back:function () {
        this.$router.go(-1);
      },
      getrule:function () {
        this.showrule=!this.showrule;
      },
      hiderule:function () {
        this.showrule=!this.showrule;
      },
      isActive:function(index){
        for(var i=0;i<this.list.length;i++){
          this.list[i].isactive=false;
          if(i==index){
            this.list[index].isactive=true;
          }
          if(index==0){
            this.single=true;
            this.fu=false;
          }else if(index==1){
            this.single=false;
            this.fu=true;
          }
        }
      },
      getHeight:function () {
        var bodyheight=document.body.clientHeight;
        console.log(bodyheight)
        const self = this;
        this.$refs.mybox.style.height=bodyheight-118+"px";
      },
    },

    components:{
      singleItem,
      fuItem,
    }
  }

</script>

<style scoped>
  #list{
    width:100%;
    height:auto;
    overflow: auto;
    margin-top: 2.35rem;
    background: #ffd6ad;
    padding-top: 0.2rem;
  }
  .list div{
    width: 2rem;
    text-align: center;
    position: relative;
  }

  .list .active{
    color:rgb(251,194,46);
    text-shadow: #000 1px 1px 1px;
  }
  .list .active:after{
    content: "";
    width: 2rem;
    height: 2px;
    background: #fff;
    position: absolute;
    bottom: 0.05rem;
    left:0;
  }
  .list{
    width: 80%;
    height: 0.9rem;
    position: absolute;
    bottom: 0;
    left: 10%;
    display: flex;
    justify-content: space-around;
    line-height: 0.85rem;
    font-size: 0.34rem;
    color: #fff;
    font-weight: 600;
  }
  .rule{
    width: 0.48rem;
    margin-right: 0.4rem;
    float: right;
    margin-top: 0.4rem;
  }
  .title{
    width: 50%;
    height: 0.7rem;
    line-height: 0.7rem;
    text-align: center;
    margin-left: 25%;
    margin-top: 0.35rem;
    float: left;
    color: #fff;
    font-weight: bold;
    font-size:0.4rem;
  }
  #back{
    width:0.6rem;
    position:absolute;
    top:0.4rem;
    left:0.4rem;
  }
  .header{
    width:100%;
    height:2.35rem;
    background-image:url("../../assets/img/huigongxiang/huigongxiang-title.png");
    background-repeat: no-repeat;
    background-size:cover;
    position:absolute;
  }
  .huigongxiang{
    width:100%;
    height:100%;
    overflow: hidden;
  }
  /**/
  .rulebox{
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.3);
    position: absolute;
    top:0;
    left:0;
    overflow-y:auto;
    z-index:4;
  }
  .rulebox div{
    width:5.6rem;
    height:6.7rem;
    position: absolute;
    top:-1rem;
    left:0;
    right:0;
    bottom:0;
    margin:auto;
    background-image: url("../../assets/img/charts/rule-bg.png");
    background-size: cover;
    background-repeat: no-repeat;
  }
  .rulebox div p{
    width:90%;
    color:#fff;
    font-size:0.24rem;
    margin:0.05rem 5%;
  }
  .rulebox div .title{
    font-size:0.32rem;
    text-align: center;
    margin:0.2rem 5%;
  }
  .close{
    width:0.8rem;
    height:0.8rem;
    position: absolute;
    bottom:-1.2rem;
    left:50%;
    margin-left:-0.4rem;
  }
</style>



