<template>
  <div class="Jackpot">
    <!--/*******************-->
    <img @click="guidetwoshow" v-show="guideone" src="../../assets/img/Jackpot/jack-guide1.png" alt="" class="guide">

    <!--/*******************-->




    <img @click="back" src="../../assets/img/huigongxiang/huigongxiang-back.png" alt="" id="back">

    <div class="header"></div>
    <div class="edu"> &nbsp;()</div>
    <div class="Jackpot-num">{{Jackpotnum}}</div>
    <ul class="Jacklist">
      <li class="Jack-scrolllist" v-for="item in scrollList"><span></span> &nbsp;{{item.nickname}} &nbsp;<span>&nbsp;</span> {{item.type}} <span>&nbsp;&nbsp;</span> {{item.money}}</li>
    </ul>


    <div class="currentPrize"> <span>{{currentPrize}}</span> </div>
    <div class="currentmoney"> <span>{{currentmoney}}</span> </div>
    <div class="person">:306</div>
    <div class="chou">
      <div v-for="(item,index) in checklist" @click="check(index)">
        <img :src="item.src" alt="">
        <img style="width: 1.8rem;height: 0.36rem;" :src="item.title" alt="">
      </div>
    </div>


    <div class="pop-fuli" v-show="WelfareHb" >
      <div v-for="(item,index) in Welfare" v-show="item.prize" class="pop-fuli-jack" :style="'backgroundImage:url('+item.Welfarebg+')'">
        <img @click="chaiWelfareHb(index)" src="../../assets/img/Jackpot/jack-chaiBtn.png" alt="" class="chaiBtn">
        <img @click="WelfareHbclose" src="../../assets/img/Jackpot/jack-close.png" alt="" class="jack-close">
      </div>
      <div v-show="prizelist" class="popprize">
        <div class="money">{{money}}</div>
        <img @click="WelfareHbclose" src="../../assets/img/Jackpot/jack-shouBtn.png" alt="" class="chaiBtn">
        <img @click="WelfareHbclose" src="../../assets/img/Jackpot/jack-close.png" alt="" class="jack-close">
      </div>
    </div>

    <img @click="jumptixian" src="../../assets/img/Jackpot/Jackpot-tixianBtn.png" alt="" class="tixianBtn">
    <img @click="getrule" src="../../assets/img/Jackpot/Jackpot-rule.png" alt="" class="rule">
    <!---->
    <div class="rulebox" v-show="showrule">
      <div>
        <p class="title"></p>
        <p>1."",,()</p>
        <p>2.,50()</p>
        <p>3.,,</p>
        <p>4.,</p>
        <img @click="hiderule" src="../../assets/img/index/index-close.png" alt="" class="close">
      </div>
    </div>
  </div>
</template>


<script>
  import apicommon from "../../assets/common/js/api.js";
  import mui from 'mui'; // 
  export default {
    data(){
      return{
        guideone:true,
        userId:localStorage.getItem('userId'),//id
        Jackpotnum:"",//
        scrollList:[],
        currentPrize:"",
        checklist:[
          {title:require("../../assets/img/Jackpot/Jacket-50.png"),src:require("../../assets/img/Jackpot/Jackpot-fuli.png"),},
          {title:require("../../assets/img/Jackpot/Jacket-100.png"),src:require("../../assets/img/Jackpot/Jackpot-xingyun.png"),},
          {title:require("../../assets/img/Jackpot/Jacket-200.png"),src:require("../../assets/img/Jackpot/Jackpot-jingxi.png"),}
        ],
        WelfareHb:false,
        prize:true,
        Welfare:[
          {prize:false,Welfarebg:require("../../assets/img/Jackpot/jack-prizebg.png"),},
          {prize:false,Welfarebg:require("../../assets/img/Jackpot/jack-prizebg.png"),},
          {prize:false,Welfarebg:require("../../assets/img/Jackpot/jack-prizebg.png"),}
        ],
        prizelist:false,
        flagJack:"",//
        flagprize:"",//
        money:"",//
        showrule:false,
        currentmoney:"0.00",
      }
    },

    created:function(){
      //
//      mui.back = function(){
//        history.go(-1);
//      }
      //
      setInterval(this.scroll,1500)
      //
      this.JackNum();
      setInterval(this.JackNum,5000)
      //
      this.getprizenum();
      this.getmoney();
      var urlprizeinfo=apicommon.apicommon+"/test/index.php/Activity/Game/prizeinfo";
      this.axios.post(urlprizeinfo).then(successRes=>{
        this.scrollList=successRes.data.data;
      })
    },

    methods:{
      guidetwoshow:function () {
        this.guideone=!this.guideone;
        this.money="3.00";
        this.WelfareHb=!this.WelfareHb;
        this.prizelist=!this.prizelist;
      },
      getrule:function () {
        this.showrule=!this.showrule;
      },
      hiderule:function () {
        this.showrule=!this.showrule;
      },
      back:function () {
        this.$router.go(-1)
      },
      jumptixian:function () {
        this.$router.push('/enchashment')
      },
      check:function (index) {
        this.WelfareHb=true;
        this.Welfare[index].prize=true;
        var url=apicommon.apicommon+"/test/index.php/Activity/Game/lotteryjudge";
        var type=index+1;
        var that=this;
        this.axios.post(url,this.$qs.stringify({uid:this.userId,type:type})).then(successRes=>{
          console.log(successRes.data);
          if(successRes.data.status==2){
            console.log("");
            that.flagJack=false;
          }else if(successRes.data.status==3){
            console.log("");
            that.flagprize=false;
          }else if(successRes.data.status==1){
            that.flagJack=true;
            that.flagprize=true;
          }
        })
      },

      chaiWelfareHb:function(index){
        if(!this.flagJack){
          alert(",");
          return;
        }else if(!this.flagprize){
          confirm(",","",function () {
            return ;
          });
        }else if(this.flagprize&&this.flagJack){
          var url=apicommon.apicommon+"/test/index.php/Activity/Game/moneylottery";
          var type=index+1;
          this.axios.post(url,this.$qs.stringify({uid:this.userId,type:type})).then(successRes=>{
            this.money="";
            if(successRes.data.status===1){
              this.money=successRes.data.money;
              this.getprizenum();
              this.getmoney();
            }
          });
          this.prize=false;
          this.prizelist=true;

        }
      },


      WelfareHbclose:function () {
        this.WelfareHb=false;
        this.prizelist=false;
      },

      scroll(){
        this.animate=true;    // css3true
        setTimeout(()=>{      //  es6this
          this.scrollList.push(this.scrollList[0]);  // 
          this.scrollList.shift();               //
          this.animate=false;  // margin-top 0 
        },100)
      },
      JackNum(){
        var url=apicommon.apicommon+"/test/index.php/Activity/Game/showmoneysum";
        this.axios.post(url).then(successRes=>{
          var result=successRes.data.data;
          this.Jackpotnum=result.shiwan+result.wan+result.qian+result.bai+result.shi+result.ge;
        });
      },
      getprizenum(){
        var url=apicommon.apicommon+"/test/index.php/Activity/Game/getnowscore";
        this.axios.post(url,this.$qs.stringify({uid:this.userId})).then(successRes=>{
          this.currentPrize=successRes.data.scroe;
        });
      },
      getmoney:function () {
        var url=apicommon.apicommon+"/test/index.php/Activity/Game/getnowprice";
        this.axios.post(url,this.$qs.stringify({uid:this.userId})).then(successRes=>{
          if(successRes.data.status===1){
            this.currentmoney=successRes.data.nowprice;
          }
        })
      },
    },


  }
</script>

<style scoped>
  /**************/
  .guide{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    z-index: 5;
  }
  #back{
    width:0.6rem;
    position:absolute;
    top:0.4rem;
    left:0.4rem;
  }
  /**/
  .pop-fuli{
    width:100%;
    height: 100%;
    background: rgba(0,0,0,0.6);
    position: absolute;
    top:0;
    z-index: 5;
  }
  .pop-fuli-jack{
    width: 7.5rem;
    height:10.14rem;
    position: absolute;
    top:1.3rem;
    background-size: cover;
    background-repeat: no-repeat;
  }
  .popprize{
    width: 7.5rem;
    height:10.14rem;
    position: absolute;
    top:1.3rem;
    background-image: url("../../assets/img/Jackpot/jack-prize2.png");
    background-size: cover;
    background-repeat: no-repeat;
  }

  .chaiBtn{
    width:3.46rem;
    height:0.8rem;
    position: absolute;
    top:6.5rem;
    left:50%;
    margin-left:-1.73rem;
  }
  .jack-close{
    width:0.4rem;
    height:0.4rem;
    position: absolute;
    bottom: 1rem;
    left: 50%;
    margin-left: -0.2rem;
  }
  /**/
  .fadetransition{
    transition: all .5s cubic-bezier(.55,0,.1,1);
  }
  .fade-enter-active, .fade-leave-active {
    -webkit-transform: translate(0px, 50px);
    transform: translate(0px, 50px);
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    -webkit-transform: translate(0,-50px);
    transform: translate(0,-50px);
  }

  .money{
    position: absolute;
    top: 3.1rem;
    width: 100%;
    text-align: center;
    font-size: 0.65rem;
    text-indent: -0.5rem;
    color: #cdab67;
  }
  .edu{
    font-size: 0.24rem;
    color: #fff;
    position: absolute;
    top: 2.65rem;
    width: 100%;
    text-align: center;
  }

  .Jackpot{
    width:100%;
    height:100%;
    background-image:url("../../assets/img/Jackpot/enchashment-newbj.jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
  }
  .header{
    width:6.16rem;
    height:6.32rem;
    position: absolute;
    top: 2.3rem;
    left:50%;
    margin-left:-3.13rem;
    background-image:url("../../assets/img/Jackpot/Jackpot-header.png");
    background-size: cover;
    background-repeat: no-repeat;
  }
  .Jackpot-num{
    width: 100%;
    height: 0.5rem;
    font-size: 0.48rem;
    color: rgb(255,247,31);
    letter-spacing: 0.27rem;
    position: absolute;
    top: 3.3rem;
    line-height: 0.5rem;
    text-align: center;
    text-indent: 0.24rem;
  }
  .Jacklist{
    width: 5.8rem;
    height: 2rem;
    position: absolute;
    top: 6.2rem;
    left: 0.8rem;
    list-style: none;
    padding-top:0.05rem;
    overflow: hidden;
  }
  .Jacklist li{
    width: 90%;
    margin-left: 5%;
    height: 0.5rem;
    line-height: 0.5rem;
    text-align: center;
    /*border-top: 1px dashed #fff;*/
    font-size: 0.26rem;
    color:red;
  }
  .Jacklist li span{
    color:#fff;
  }

  /**/
  .currentPrize{
    width:1.88rem;
    height:0.82rem;
    background-image: url("../../assets/img/Jackpot/Jacket-currentyubi.png");
    -webkit-background-size: cover;
    background-size: cover;
    background-repeat: no-repeat;
    text-indent: 0.2rem;
    line-height: 0.8rem;
    position: absolute;
    bottom: 0.6rem;
    left: 0.25rem;
    font-size: 0.3rem;
    color: #451206;
  }
  .currentmoney{
    width:1.88rem;
    height:0.82rem;
    background-image: url("../../assets/img/Jackpot/Jacket-current-money.png");
    -webkit-background-size: cover;
    background-size: cover;
    background-repeat: no-repeat;
    text-indent: 0.2rem;
    line-height: 0.8rem;
    position: absolute;
    bottom: 0.6rem;
    left: 2.3rem;
    font-size: 0.3rem;
    color: #451206;
  }

  .currentmoney span{
    float: left;
    margin-left: 0.5rem;
  }
  .currentPrize span{
    float: left;
    margin-left: 0.5rem;
  }
  /**/
  .chou{
    width: 100%;
    height: auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    bottom: 1.5rem;
  }
  .chou div{
    flex: 1;
    text-align: center;
  }
  .chou img{
    width:1.88rem;
    height:2.08rem;
  }
  .chou p{
    width: 100%;
    text-align: center;
    margin-top: -0.15rem;
    color: #fff;
  }

  .tixianBtn{
    width: 1.98rem;
    height: 0.82rem;
    position: absolute;
    bottom: 0.6rem;
    left: 4.3rem;
  }
  .rule{
    width: 0.8rem;
    height: 0.82rem;
    position: absolute;
    bottom: 0.6rem;
    right: 0.3rem;
  }
  .person{
    color: #fff;
    font-size: 0.24rem;
    position: absolute;
    top: 4.2rem;
    text-align: center;
    width: 100%;
  }

  /**/
  .rulebox{
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.3);
    position: absolute;
    top:0;
    left:0;
    overflow-y:auto;
  }
  .rulebox div{
    width:5.6rem;
    height:6.7rem;
    position: absolute;
    top:-1rem;
    left:0;
    right:0;
    bottom:0;
    margin:auto;
    background-image: url("../../assets/img/charts/rule-bg.png");
    background-size: cover;
    background-repeat: no-repeat;
  }
  .rulebox div p{
    width:90%;
    color:#fff;
    font-size:0.24rem;
    margin:0.05rem 5%;
  }
  .rulebox div .title{
    font-size:0.32rem;
    text-align: center;
    margin: 0.2rem 5% 0.4rem 5%;
  }
  .close{
    width:0.8rem;
    height:0.8rem;
    position: absolute;
    bottom:-1.2rem;
    left:50%;
    margin-left:-0.4rem;
  }
</style>



