<template>
  <div class="loginbox">
    <header class="mui-bar mui-bar-nav">
      <h1 class="mui-title"></h1>
    </header>
    <div class="mui-content">
      <form id='login-form' class="mui-input-group">
        <div class="mui-input-row">
          <label></label>
          <input id='account' type="text" class="mui-input-clear mui-input" placeholder="" v-model="tel">
        </div>
        <div class="mui-input-row">
          <label></label>
          <input id='password' type="password" class="mui-input-clear mui-input" placeholder="" v-model="password">
        </div>
      </form>

      <div class="mui-content-padded">
        <button @click="login()" id='login'></button>
        <div class="link-area">
          <router-link to="/login/register" id='reg'></router-link>
          <span class="spliter">|</span>
          <router-link to="/login/forgetPassword" id='forgetPassword'></router-link>
        </div>
      </div>
      <!--<div class="mui-content-padded oauth-area">-->

    </div>
  </div>

</template>

<script>
  import apicommon from "../../assets/common/js/api.js";
  import md5 from 'js-md5';
  import mui from 'mui';
  export default{
    data(){
      return{
        tel:"",
        password:"",
      }
    },
    created:function(){
//      //
//      //1
//      var first = null;
//      mui.back = function() {
//        //
//        if (!first) {
//          first = new Date().getTime();
//          mui.toast('');
//          setTimeout(function() {
//            first = null;
//          }, 1000);
//        } else {
//          if (new Date().getTime() - first < 1000) {
//            plus.runtime.quit();
//          }
//        }
//      };
    },
    methods:{
      login:function(){
        console.log(this)
        var url=apicommon.apicommon+"/test/index.php/Activity/MobileList/login";
        var data={tel:this.tel,password:md5(this.password)};
        this.axios.post(url,this.$qs.stringify(data)).then(successRes=>{
//          console.log(successRes);
          var that=this;
          if(successRes.data.status=="0"){
            alert("");
          }else if(successRes.data.status=="-1"){
            alert(",");
          }else{
            localStorage.setItem('Token', "login");
            localStorage.setItem('userId',successRes.data.status );
            that.$router.push('/index');
          }
        })
      }
    }
  }


</script>

<style scoped>
  body {
    background: #F5F5F5;
  }
  .mui-bar-nav ~ .mui-content {
    padding-top: 60px;
    font-size:14px;
  }
  .mui-input-row label ~ input{
   font-size:14px;
  }
  .mui-btn-block {
    padding: 8px 0;
    font-size: 16px;
  }
  .link-area {
    display: block;
    margin-top: 25px;
    text-align: center;
  }


  .spliter {
    color: #bbb;
    padding: 0px 8px;
  }

  .oauth-area {
    position: absolute;
    bottom: 20px;
    left: 0px;
    text-align: center;
    width: 100%;
    padding: 0px;
    margin: 0px;
  }

  .oauth-area .oauth-btn {
    display: inline-block;
    width: 50px;
    height: 50px;
    background-size: 30px 30px;
    background-position: center center;
    background-repeat: no-repeat;
    margin: 0px 20px;
    /*-webkit-filter: grayscale(100%); */
    border: solid 1px #ddd;
    border-radius: 25px;
  }

  .oauth-area .oauth-btn:active {
    border: solid 1px #aaa;
  }

  .oauth-area .oauth-btn.disabled {
    background-color: #ddd;
  }
  #login{
    color: #fff;
    background: #ec5155;
    border: none;
    border-radius: 8px;
    width:95%;
    padding:10px;
    margin:0 2.5%;
    font-size: 0.3rem;
  }
</style>



