<template>
  <div class="charts">
    <div class="header">
      <img @click="back" src="../../assets/img/huigongxiang/huigongxiang-back.png" alt="" id="back">
      <img src="../../assets/img/charts/charts-title.png" alt="" class="title">
      <img @click="getrule" src="../../assets/img/huigongxiang/huigongxiang-rule.png" alt="" class="rule">
    </div>
    <!--/**/-->
    <div class="rulebox" v-show="showrule">
      <div>
        <p class="rule-title"></p>
        <table border="1">
          <tr>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr>
            <td>0-1000</td>
            <td>0-100</td>
            <td></td>
          </tr>
          <tr>
            <td>1000-3000</td>
            <td>100-300</td>
            <td></td>
          </tr>
          <tr>
            <td>3000-10000</td>
            <td>300-1000</td>
            <td></td>
          </tr>
          <tr>
            <td>10000-25000</td>
            <td>1000-2500</td>
            <td></td>
          </tr>
          <tr>
            <td>25000-50000</td>
            <td>2500-5000</td>
            <td></td>
          </tr>
          <tr>
            <td>50000</td>
            <td>5000</td>
            <td></td>
          </tr>
        </table>
        <p></p>
        <img @click="hiderule" src="../../assets/img/index/index-close.png" alt="" class="close">
      </div>
    </div>

    <img src="../../assets/img/charts/charts-header.png" alt="" class="sekuai">
    <div class="myself">
      <div class="headimg" :style="'backgroundImage:url('+myinfo.headimg+')'"></div>
      <img class="dengji" :src="myinfo.pic" alt="">
      <span class="nickname" style="display: block;position: absolute;top: 0.2rem;left:1.9rem;">{{myinfo.nickname}}</span>
      <img class="jifen" src="../../assets/img/index/index-prize.png" alt="">
      <span style="display: block;position: absolute;top: 0.73rem;left: 1.9rem;">{{myinfo.experience}}</span>
      <span style="display: block;position: absolute;top: 0.4rem;right: 0.5rem;font-size: 0.34rem;">{{myinfo.rank}}</span>
    </div>
    <ul>
      <li class="list" v-for="(item,index) in list">
        <img id="num" :src="item.rankpic" alt="">
        <img v-show="index==0" src="../../assets/img/charts/1.png" alt="" class="first">
        <img v-show="index==1" src="../../assets/img/charts/2.png" alt="" class="first">
        <img v-show="index==2" src="../../assets/img/charts/3.png" alt="" class="first">
        <div class="headimg-list" :style="'backgroundImage:url('+item.headimg+')'"></div>
        <img style="width: 0.48rem;position: absolute;top: 0.4rem;left: 5.2rem;" :src="item.pic" alt="">
        <span class="nickname" style="display: block;position: absolute;top: 0.44rem;left: 2rem;">{{item.nickname}}</span>
        <span style="display: block;position: absolute;top: 0.4rem;right: 0.5rem;font-size: 0.34rem;">{{item.experience}}</span>
      </li>
    </ul>
  </div>
</template>
<script>
  import apicommon from "../../assets/common/js/api.js";
  import mui from 'mui'; // 
  export default {
    data(){
      return{
        userId:localStorage.getItem('userId'),
        myinfo:{},
        list:[],
        showrule:false,
      }
    },

    created:function(){


//      //
//      mui.back = function(){
//        history.go(-1);
//      };

      var url=apicommon.apicommon+"/test/index.php/Activity/Game/rank";
      this.axios.post(url,this.$qs.stringify({uid:this.userId})).then(successRes=>{
        this.myinfo=successRes.data.my;
        this.list=successRes.data.data;
      })
    },

    methods:{
      back:function () {
        this.$router.go(-1)
      },
      getrule:function () {
        this.showrule=!this.showrule;
      },
      hiderule:function () {
        this.showrule=!this.showrule;
      },
    },

  }
</script>

<style scoped>
  table{
    width: 90%;
    height: auto;
    color: #fff;
    font-size: 0.24rem;
    border: 1px solid #2538be;
    border-radius: 5px;
    margin: 0.3rem 5% 0.1rem 5%;
  }
  tr{
    height:0.5rem;
    line-height: 0.5rem;
  }
  td{
    text-align: center;
  }
  .charts{
    width:100%;
    height:100%;
    position: absolute;
    background:rgb(247,188,130);
  }
  .rule{
    width: 0.48rem;
    margin-right: 0.3rem;
    float: right;
    margin-top: 0.3rem;
  }
  #back{
    width:0.6rem;
    position:absolute;
    top:0.25rem;
    left:0.3rem;
  }
  .title{
    width:1.58rem;
    height:0.58rem;
    position:absolute;
    top:0.2rem;
    left:50%;
    margin-left:-0.79rem;
  }

  .header{
    width:100%;
    height:1.64rem;
    background:#ffd876;
  }
  .sekuai{
    width:100%;
    height:1.37rem;
  }
  .myself{
    width:7.21rem;
    height:1.35rem;
    background-image:url("../../assets/img/charts/charts-list.png");
    background-size:cover;
    background-repeat: no-repeat;
    position: absolute;
    top: 0.9rem;
    left: 50%;
    margin-left: -3.605rem;
  }
  .my-charts{
    width:0.56rem;
    height:0.56rem;
    background:#f0a03c;
    border-radius:50%;
  }
  .headimg{
    width:0.8rem;
    height:0.8rem;
    position:absolute;
    top:0.28rem;
    left:0.28rem;
    border:1px solid #fff;
    box-sizing: border-box;
    border-radius:50%;
    background-position: center;
    background-size: 100% 100%;
  }
  .dengji{
    width: 0.48rem;
    position: absolute;
    top: 0.2rem;
    left: 1.3rem;
  }
  .jifen{
    width: 0.48rem;
    position: absolute;
    top: 0.7rem;
    left: 1.3rem;
  }
  ul{
    width:100%;
    height:auto;
    overflow: hidden;
    position: absolute;
    top:3rem;
    background: rgb(247,188,130);
  }
  ul .list{
    width:7.21rem;
    height:1.35rem;
    background-image:url("../../assets/img/charts/charts-list.png");
    background-size:cover;
    background-repeat: no-repeat;
    position: relative;
    margin:0.2rem auto auto auto;
  }
  .headimg-list{
    width:0.8rem;
    height:0.8rem;
    position:absolute;
    top:0.28rem;
    left:1rem;
    border:1px solid #fff;
    box-sizing: border-box;
    border-radius:50%;
    background-position: center;
    background-size: 100% 100%;
  }
  #num{
    width: 0.56rem;
    position: absolute;
    top: 0.35rem;
    left: 0.3rem;
  }
  .first{
    width: 0.48rem;
    position: absolute;
    top: 0.1rem;
    left: 0.8rem;
    z-index: 2;
  }
  /**/
  .rulebox{
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.3);
    position: fixed;
    top:0;
    left:0;
    z-index:3;
  }
  .rulebox div{
    width:5.6rem;
    height:6.7rem;
    position: absolute;
    top:-1rem;
    left:0;
    right:0;
    bottom:0;
    margin:auto;
    background-image: url("../../assets/img/charts/rule-bg.png");
    background-size: cover;
    background-repeat: no-repeat;
  }
  .rulebox div p{
    width:90%;
    color:#fff;
    font-size:0.24rem;
    margin:0.05rem 5%;
  }
  .rulebox div .rule-title{
    font-size:0.32rem;
    text-align: center;
    margin:0.2rem 5%;
  }
  .close{
    width:0.8rem;
    height:0.8rem;
    position: absolute;
    bottom:-1.2rem;
    left:50%;
    margin-left:-0.4rem;
  }
</style>



